@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;900&display=swap);
html,
body {
  font-family: "Montserrat", sans-serif;
  margin: 0;
  height: 100%;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
}

.background-about {
  background-image: linear-gradient(
      150deg,
      rgba(56, 40, 161, 0.75) 0%,
      rgba(10, 7, 20, 0.75) 100%
    ),
    url(/static/media/background-blurred.8ea1e1eb.jpg);
}
.background-projects {
  background-image: linear-gradient(
      150deg,
      rgba(249, 135, 4, 0.5) 0%,
      rgba(56, 51, 36, 0.6) 100%
    ),
    url(/static/media/background-blurred.8ea1e1eb.jpg);
}

.background-contact {
  background-image: linear-gradient(
      150deg,
      rgba(128, 217, 90, 0.75) 0%,
      rgba(21, 41, 15, 0.75) 100%
    ),
    url(/static/media/background-blurred.8ea1e1eb.jpg);
}

.App{text-align:left;width:1366px;height:768px;display:flex}.App .route-content-container{background-color:white;width:75%;height:95%;border-radius:0 15px 15px 0;box-shadow:3px 3px 15px 0px rgba(0,0,0,0.24)}

.sidebar-button{width:90%;height:55px;text-decoration:none;font-weight:600;display:flex;margin:5px 0;align-items:center;color:black;align-self:flex-end;transition-duration:0.1s}.sidebar-button:hover{border-radius:5px 0 0 5px}.sidebar-button:hover.about{background-color:#5c52a0}.sidebar-button:hover.projects{background-color:#f6b56a}.sidebar-button:hover.contact{background-color:#b2d6a3}.sidebar-button.selected{color:white;border-radius:5px 0 0 5px}.sidebar-button.selected.about{background-color:#3828a1}.sidebar-button.selected.projects{background-color:#f98704}.sidebar-button.selected.contact{background-color:#7ad851}.sidebar-button .button-icon{background-size:cover;width:30px;height:30px;margin:0 10px}.sidebar-button .button-icon.about{background-image:url(/static/media/about.06def363.svg)}.sidebar-button .button-icon.projects{background-image:url(/static/media/projects.e7c39562.svg)}.sidebar-button .button-icon.contact{background-image:url(/static/media/contact.9f944c02.svg)}

.sidebar-container{background-color:rgba(216,232,246,0.6);width:20%;height:95%;border-radius:15px 0px 0px 15px;display:flex;flex-direction:column;justify-content:space-evenly;box-shadow:-3px 3px 15px 1px rgba(0,0,0,0.24)}.sidebar-container .sidebar-image{margin:10px auto 0 auto;width:100px;height:100px;background-image:url(/static/media/profile.beae6dd2.svg);background-size:cover}.sidebar-container .sidebar-buttons-container{display:flex;flex-direction:column;height:60%}.sidebar-container .sidebar-hr{width:90%;border-top:1px solid #3828a1}.sidebar-container .sidebar-hr.about{border-top:1px solid #3828a1}.sidebar-container .sidebar-hr.projects{border-top:1px solid #f98704}.sidebar-container .sidebar-hr.contact{border-top:1px solid #7ad851}

